.Contact {
	.inner {
		.map-wrap {
			position: relative;
			#map {
				width: 100%;
				height: 400px;
				background: #111;
				position: relative;
			}
			h2 {
				color: dodgerblue;
				margin: 30px 0;
			}
			.branch {
				position: absolute;
				bottom: 0;
				right: 0;
				z-index: 3;
				display: flex;
				li {
					padding: 10px;
					background-color: dodgerblue;
					color: #fff;
					cursor: pointer;
					text-align: center;
					&.on {
						background-color: deepskyblue;
					}
				}
			}
			.btnToggle {
				position: absolute;
				top: 0;
				right: 0;
				padding: 10px;
				height: 50px;
				border-radius: 10px;
				line-height: 30px;
				background-color: dodgerblue;
				color: #fff;
				cursor: pointer;
				text-align: center;
				z-index: 2;
			}
		}
		.contact-form {
			width: 100%;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			form {
				width: 90%;
				max-width: 600px;

				.input-group {
					color: #333;
					margin-bottom: 15px;

					input,
					textarea {
						padding: 10px;
						width: 100%;
						border: 2px solid dodgerblue;
						font-size: 15px;
					}
					label {
						color: dodgerblue;
						font-weight: bold;
					}

					button {
						width: 100%;
						padding: 10px;
						border: 2px solid dodgerblue;
						color: dodgerblue;
						font-weight: bold;
						cursor: pointer;
						background: #fff;
					}
				}

				.row {
					display: flex;
					align-items: center;
					justify-content: space-between;
					.input-group {
						flex-basis: 48%;
					}
				}
			}
		}
	}
}
